<template>
  <div class="dbv1">
    <megReturn
      @click="jump('statistical')"
    >
      
    </megReturn>
    <div class="db mb20">
      <div class="fx1"></div>
      <el-button
        v-if="isAdmin"
        size="medium"
        type="primary"
        @click="addStatistical"
      >
        /
      </el-button>
    </div>
    <addStatistical
      :consumerId="consumerId"
      :contractId="softInfo.contract_id"
      :visible.sync="visibleStatical"
      @enter="getData"
    ></addStatistical>
    <div>
      <el-table
        border
        :data="tableData"
      >
        <el-table-column
          label=""
          width="300px"
          prop="sdk_type_name"
        >
        </el-table-column>
        <el-table-column
          label=""
          prop="contract_type"
        >
        </el-table-column>
        <el-table-column
          label=""
          prop="authorized_days"
        >
        </el-table-column>
        <el-table-column
          label=""
          prop="activated_count"
        >
        </el-table-column>
        <el-table-column
          label=""
          prop="remaining_count"
        >
        </el-table-column>
      </el-table>
    </div>

    <megTitle class="mt20 mb20">
      
    </megTitle>
    <el-scrollbar>
      <el-table
        border
        :data="changeTableData"
      >
        <el-table-column
          label=""
          :formatter="formatterDateTime"
          prop="changed_time"
        >
        </el-table-column>
        <el-table-column
          label=""
          prop="changed_detail"
        >
        </el-table-column>
        <el-table-column
          label=""
          prop="changed_reason"
        >
        </el-table-column>
      </el-table>
    </el-scrollbar>
  </div>
</template>

<script>
import megReturn from '@/components/megReturn.vue';
import megTitle from '@/components/megTitle.vue';
import mixins from '@/common/mixins/util.js';
import addStatistical from './addStatistical';


export default {
  name: 'AuthChange',
  components: {
    megReturn,
    megTitle,
    addStatistical
  },
  mixins: [mixins],
  props: {
    softInfo: {
      type: Object,
      default() {
        return [];
      }
    },
    consumerId: {
      type: Number,
      default: null
    },
    isAdmin: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      changeTableData: [],
      visibleStatical: false
    };
  },
  computed: {
    tableData() {
      return [this.softInfo];
    },
    userType() {
      return this.isAdmin ? 'admin' : 'user';
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    jump() {
      this.$emit('jump', 'statistic');
    },
    getData() {
      this.$service.statistical[`${this.userType}GetChangeHistory`]({
        consumer_id: this.consumerId,
        contract_id: this.softInfo.contract_id,
        admin_id: this.$store.getters['user/getUserInfo'].id
      }).then(data => {
        this.changeTableData = data.data || [];
      });
    },
    addStatistical() {
      this.visibleStatical = true;
    }
  }
};
</script>
