<template>
  <div class="namespace-manage">
    <megTitle></megTitle>
    <el-button
      type="primary"
      class="add-btn"
      @click="openDialog"
    >
      /namespace
    </el-button>
    <el-button
      type="primary"
      class="add-btn"
      @click="openOwnerDialog"
    >
      namespaceOwner
    </el-button>
    <el-dialog
      title="Owner"
      :visible.sync="ownerDialogVisible"
      width="30%"
      class="dialogClass"
    >
      <el-form
        :model="ownerForm"
        :rules="addOwnerRules"
      >
        <el-form-item
          label="Owner"
          label-width="100px"
          prop="owner_id"
        >
          <el-input v-model="ownerForm.owner_id"></el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="ownerDialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="addNamespaceOwner"
        > </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="newDialogVisible"
      width="30%"
      class="dialogClass"
    >
      <el-form
        :model="form"
        :rules="addRules"
      >
        <el-form-item
          label=""
          label-width="100px"
          prop="name"
        >
          <el-input v-model="form.name"></el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="newDialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="addNamespace"
        > </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="40%"
      class="dialogClass"
    >
      <el-form
        :model="form"
        label-position="left"
      >
        <el-form-item
          label=""
          label-width="100px"
        >
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item
          label="Owner"
          label-width="100px"
        >
          <el-input v-model="form.owners"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          label-width="100px"
        >
          <el-input v-model="form.writers"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          label-width="100px"
        >
          <el-input v-model="form.supporters"></el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="editNamespace"
        > </el-button>
      </span>
    </el-dialog>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
    >
      <el-table-column
        prop="name"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="owners"
        label=""
        :formatter="format"
      >
      </el-table-column>
      <el-table-column
        prop="writers"
        label=""
        :formatter="format"
      >
      </el-table-column>
      <el-table-column
        prop="supporters"
        label=""
        :formatter="format"
      >
      </el-table-column>
      <el-table-column
        prop="address"
        label=""
      >
        <template slot-scope="scope">
          <el-button
            type="text"
            @click="changeNamespace(scope.$index, scope.row)"
          >
            
          </el-button>
          <el-button
            type="text"
            @click="deleteNamespace(scope.$index, scope.row, tableData)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import megTitle from '@/components/megTitle';
export default {
  name: 'NamespaceManage',
  components: { megTitle },
  directives: {
    focus: {// v-focus
      inserted(el) {
        el.querySelector('input').focus();
      }
    }
  },
  data() {
    return {
      index: '',
      admin_id: 1,
      namespace_id: '',
      ownerDialogVisible: false,
      newDialogVisible: false,
      editDialogVisible: false,
      tableData: [],
      ownerForm: {
        name: ''
      },
      form: {
        name: '',
        owners: '',
        writers: '',
        supporters: ''
      },
      addRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      addOwnerRules: {
        owner_id: [
          {
            required: true, message: 'Owner', trigger: 'blur'
          }
        ]
      }
    };
  },
  mounted() {
    this.getNamespaceList();
  },
  methods: {
    //
    getNamespaceList() {
      this.$service.namespaceManage.getList({
        admin_id: this.$store.getters['user/getAdminId']
      }).then((data) => {
        console.log(data);
        this.tableData = data.data;
      })
        .catch((error) => {
          console.log(error);
        });
    },
    openDialog() {
      this.form = {};
      this.newDialogVisible = true;
    },
    openOwnerDialog() {
      this.ownerForm = {};
      this.ownerDialogVisible = true;
    },
    //
    addNamespaceOwner() {
      this.$service.namespaceManage.addOwener({
        admin_id: this.$store.getters['user/getAdminId'],
        namespace_id: this.namespace_id,
        owner_id: this.ownerForm.owner_id,
      }).then((response) => {
        console.log(response);
        if (response.status === 200) {
          this.ownerDialogVisible = false;
          console.log('');
        }
      })
        .catch((error) => {
          console.log(error);
          console.log('');
        });
    },
    //
    addNamespace() {
      this.$service.namespaceManage.creat({
        admin_id: this.$store.getters['user/getAdminId'],
        name: this.form.name
      }).then((response) => {
        console.log(response);
        if (response.status === 200) {
          this.newDialogVisible = false;
          console.log('');
          const list = {
            name: this.form.name
          };
          this.tableData.push(list);
        }
      })
        .catch((error) => {
          console.log(error);
          console.log('');
        });
    },
    editNamespace() {
      const owners = this.form.owners.toString().split(',');
      const writers = this.form.writers.toString().split(',');
      const supporters = this.form.supporters.toString().split(',');
      this.$service.namespaceManage.update({
        admin_id: this.$store.getters['user/getAdminId'],
        namespace_id: this.namespace_id,
        name: this.form.name,
        owners,
        writers,
        supporters
      }).then((response) => {
        console.log(response);
        if (response.status === 200) {
          this.editDialogVisible = false;
          console.log('');
          this.tableData[this.index].name = this.form.name;
          this.tableData[this.index].owners = this.form.owners;
          this.tableData[this.index].writers = this.form.writers;
          this.tableData[this.index].supporters = this.form.supporters;
        }
      })
        .catch((error) => {
          console.log(error);
          console.log('');
        });
    },

    //
    changeNamespace(index, row) {
      console.log(row);
      console.log('index:' + index);
      this.index = index;
      this.editDialogVisible = true;
      this.namespace_id = row.id;
      console.log(this.namespace_id);
      this.form.name = row.name;
      this.form.owners = row.owners;
      this.form.writers = row.writers;
      this.form.supporters = row.supporters;
    },
    //
    deleteNamespace(index, row, table) {
      console.log(row.id);
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$service.namespaceManage.delete({
          admin_id: this.$store.getters['user/getAdminId'],
          namespace_id: row.id,
        }).then((response) => {
          console.log(response);
          if (response.status === 200) {
            table.splice(index, 1);
            console.log('');
          }
        })
          .catch((error) => {
            console.log(error);
            console.log('');
          });
      }).catch(() => {
      });
    },
    //tablevalue
    format(row, col, value) {
      return (value instanceof Array) ? String(value) : value;
    }
  }
};
</script>
