<template>
  <el-dialog
    title=""
    :visible.sync="visible"
    width="500px"
    :before-close="handleClose"
  >
    <el-form
      ref="form"
      :rules="rules"
      :model="form"
      label-width="100px"
    >
      <el-form-item
        label=""
        prop="oldPassword"
      >
        <el-input v-model="form.oldPassword"></el-input>
      </el-form-item>
      <el-form-item
        label=""
        prop="newPassword"
      >
        <el-input v-model="form.newPassword"></el-input>
      </el-form-item>
      <el-form-item
        label=""
        prop="newPasswordConfirm"
      >
        <el-input v-model="form.newPasswordConfirm"></el-input>
      </el-form-item>
    </el-form>
  
    <span
      slot="footer"
      class="dialog-footer"
    >
      <el-button @click="handleClose"> </el-button>
      <el-button
        type="primary"
        @click="enter"
      > </el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'EditPassword',
  props: {
    visible: {
      type: Boolean,
      default() {
        return false;
      },
    },
    userId: {
      type: String,
      default: ''
    },
    form: {
      type: Object,
      default() {
        return {
          oldPassword: '',
          newPassword: '',
          newPasswordConfirm: ''
        };
      }
    }
  },
  data() {
    return {
      rules: {
        oldPassword: [
          { required: true, message: '', trigger: 'blur' },
        ],  
        newPassword: [
          { required: true, message: '', trigger: 'blur' },
        ], 
        newPasswordConfirm: [
          { required: true, message: '', trigger: 'blur' },
        ], 
      }
    };
  },
  methods: {
    handleClose() {
      this.$emit('update:visible', false);
    },
    enter() {
      this.$refs.form.validate(() => {});
    }
  }

};
</script>
